.NoteView {
  border: 1px solid grey;
  border-radius: 5px;
  margin: 30px auto;
  padding: 5px 20px;
  width: 60%;
}

.NoteView-title {
  border-bottom: 1px solid grey;
  margin-bottom: 8px;
  padding-bottom: 5px;
}

.NoteView-button {
  border: 1px solid grey;
  border-radius: 15px;
  font-size: 14px;
  height: 30px;
  margin: 10px 30px;
  width: 15%;
}


